import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createDr(datos_tabla){

    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameDrug').value;
    var quantity = parseInt(document.querySelector('#formQuantityDrug').value);
    var supplier_price = parseInt(document.querySelector('#formSupplierDrug').value);
    var cost_price = parseInt(document.querySelector('#formCostDrug').value);
    var sale_price = parseInt(document.querySelector('#formSaleDrug').value);

    // Para saber quien crea:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
   
    db.collection("drugs").doc().set({
      'name': name,
      'quantity': quantity,
      'supplier_price': supplier_price,
      'cost_price': cost_price,
      'sale_price': sale_price,
      'created_by': userName,
      'edited_by': userName,
      'last_updated': timeStamp
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Droga
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameDrug">
            <Form.Label>Nombre:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí el nombre de la droga." />
          </Form.Group>
          <Form.Group controlId="formQuantityDrug">
            <Form.Label>Cantidad:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí la cantidad de unidades de droga." />
          </Form.Group>
          <Form.Group controlId="formSupplierDrug">
            <Form.Label>Precio Proveedor:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí el precio al proveedor.'/>
          </Form.Group>
          <Form.Group controlId="formCostDrug">
            <Form.Label>Precio Coste:</Form.Label>
            <Form.Control type="text" placeholder="Coloca aquí el precio por unidad de coste." />
          </Form.Group>
          <Form.Group controlId="formSaleDrug">
            <Form.Label>Precio Venta:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí el precio por unidad a la venta.'/>
          </Form.Group>          
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createDr(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createdrmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createdrmodal;