import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  
  var old = props.datos.datos;

  function Editcabmodal(){
    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameGun').value;
    var quantity = parseInt(document.querySelector('#formQuantityGun').value);
    var supplier_price = parseInt(document.querySelector('#formSupplierGun').value);
    var cost_price = parseInt(document.querySelector('#formCostGun').value);
    var sale_price = parseInt(document.querySelector('#formSaleGun').value);
    var category = document.querySelector('#formCategoryGun').value;

    // Para saber quien edita:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
    var created_by = old.created_by ? old.created_by : 'valkiria';

    db.collection("guns").where("name", "==", old.name)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(function(doc) {        
        db.collection("guns").doc(doc.id).set({
          'name': name,
          'quantity': quantity,
          'supplier_price': supplier_price,
          'cost_price': cost_price,
          'sale_price': sale_price,
          'created_by': created_by,
          'edited_by': userName,
          'last_updated': timeStamp,
          'category': category
        })
        .then(function() {
          console.log("Document successfully written!");
          window.location.reload(false);
        })
        .catch(function(error) {
          console.error("Error writing document: ", error);
        });
      })
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Editar Droga
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameGun">
            <Form.Label>Nombre:</Form.Label>
            <Form.Control type="text" required defaultValue={old.name} />
          </Form.Group>
          <Form.Group controlId="formCategoryGun">
            <Form.Label>Categoría:</Form.Label>
            <Form.Control as="select" defaultValue={old.category}>              
              <option value="Corta">Corta</option>
              <option value="Corta Automática">Corta Automática</option>
              <option value="Media">Media</option>
              <option value="Media Automática">Media Automática</option>
              <option value="Larga">Larga</option>
              <option value="Larga Automática">Larga Automática</option>
            </Form.Control>
          </Form.Group> 
          <Form.Group controlId="formQuantityGun">
            <Form.Label>Cantidad:</Form.Label>
            <Form.Control type="text" required defaultValue={old.quantity} />
          </Form.Group>
          <Form.Group controlId="formSupplierGun">
            <Form.Label>Precio Proveedor:</Form.Label>
            <Form.Control type="text" defaultValue={old.supplier_price}/>
          </Form.Group>
          <Form.Group controlId="formCostGun">
            <Form.Label>Precio Coste:</Form.Label>
            <Form.Control type="text" defaultValue={old.cost_price} />
          </Form.Group>
          <Form.Group controlId="formSaleGun">
            <Form.Label>Precio Venta:</Form.Label>
            <Form.Control type="text" defaultValue={old.sale_price}/>
          </Form.Group>          
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => Editcabmodal()} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editcabmodal(datostabla) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-pencil-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
          <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        datos={datostabla}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editcabmodal;