import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createSt(datos_tabla){

    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameSt').value;
    var description = document.querySelector('#formDescSt').value;
    var owner = document.querySelector('#formOwnerSt').value;
    var key = document.querySelector('#formKeySt').value;
    var last_opened = document.querySelector('#formLastOpenSt').value;
    var location_pic = document.querySelector('#formLocationPicSt').value;
    var external_pic = document.querySelector('#formExternalPicSt').value;
    var departament = document.querySelector('#formDepartSt').value;

    // Para saber quien crea:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
   
    db.collection("estates").doc().set({
      'name': name,
      'description': description,
      'owner': owner,
      'key': key,
      'last_opened': last_opened,
      'location_pic': location_pic,
      'external_pic': external_pic,
      'departament': departament,
      'created_by': userName,
      'edited_by': userName,
      'last_updated': timeStamp
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Propiedad
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameSt">
            <Form.Label>Nombre de la Propiedad:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí le nombre de la propiedad." />
          </Form.Group>
          <Form.Group controlId="formDescSt">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control  as="textarea" rows={3} required placeholder="Coloca aquí la descripción general de la propiedad." />
          </Form.Group>
          <Form.Group controlId="formOwnerSt">
            <Form.Label>Propietario:</Form.Label>
            <Form.Control type="text" placeholder="Coloca aquí el nombre del dueño de la propiedad." />
          </Form.Group>
          <Form.Group controlId="formKeySt">
            <Form.Label>Llave:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí el número de la llave de la propiedad.'/>
          </Form.Group>
          <Form.Group controlId="formLastOpenSt">
            <Form.Label>Última Apertura: (yyyy-mm-dd)</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí la fecha de la última apertura de la propiedad.'/>
          </Form.Group>         
          <Form.Group controlId="formLocationPicSt">
            <Form.Label>Imagen mapa:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí el url de la imagen de mapa de la propiedad.'/>
          </Form.Group>
          <Form.Group controlId="formExternalPicSt">
            <Form.Label>Imagen exterior:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí el url de la imagen exterior de la propiedad.'/>
          </Form.Group>
          <Form.Group controlId="formDepartSt">
            <Form.Label>Departamento:</Form.Label>
            <Form.Control as="select" defaultValue="Personal">
              <option value="Inteligencia">Inteligencia</option>
              <option value="Intendencia">Intendencia</option>
              <option value="X-Negocios">Negocios</option>
              <option value="Operaciones">Operaciones</option>
              <option value="Personal">Personal</option>              
            </Form.Control>
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createSt(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createstmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createstmodal;