import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createCab(datos_tabla){

    // Datos metidos en el formulario:
    var id = parseInt(document.querySelector('#formIdCab').value);
    var street = document.querySelector('#formStreetCab').value;
    var crossing = document.querySelector('#formCrossingCab').value;
    var cabine_number = document.querySelector('#formCabineNumber').value;
    var cabine_pic = document.querySelector('#formCabinePic').value;

    // Para saber quien crea:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
   
    db.collection("cabines").doc().set({
      'id': id,
      'street': street,
      'crossing': crossing,
      'cabine_number': cabine_number,
      'cabine_pic': cabine_pic,
      'edited_by': userName,
      'last_updated': timeStamp
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Cabina
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formIdCab">
            <Form.Label>Id de la Cabina:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí el número de la cabina" />
          </Form.Group>
          <Form.Group controlId="formStreetCab">
            <Form.Label>Nombre de la Calle:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí el nombre de la calle donde se localiza la cabina." />
          </Form.Group>
          <Form.Group controlId="formCrossingCab">
            <Form.Label>Nombre del Cruce:</Form.Label>
            <Form.Control type="text" placeholder="Coloca aquí el nombre del cruce donde se localiza la cabina." />
          </Form.Group>
          <Form.Group controlId="formCabineNumber">
            <Form.Label>Números de la Cabina:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí el o los números de la cabina. (Separados por "/")'/>
          </Form.Group>
          <Form.Group controlId="formCabinePic">
            <Form.Label>Imagen de la Cabina:</Form.Label>
            <Form.Control type="text" placeholder='Coloca aquí la imagen de mapa de la cabina.'/>
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createCab(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createcabmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createcabmodal;