import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createUs(datos_tabla){

    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameUs').value;
    var user = document.querySelector('#formUserUs').value;
    var password = document.querySelector('#formPassUs').value;
    var departament = document.querySelector('#formDepartamentUs').value;
    var grade = document.querySelector('#formGradeUs').value;
    var profile_pic = document.querySelector('#formProfilePic').value;
    var last_conection = Date.now();

    // Para saber quien crea:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
   
    db.collection("users").doc().set({
      'name': name,
      'user': user,
      'password': password,
      'departament': departament,
      'grade': grade,
      'profile_pic': profile_pic,
      'last_conection': last_conection,
      'created_by': userName,
      'edited_by': userName,
      'last_updated': timeStamp
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Usuario
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formUserUs">
            <Form.Label>User:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí le alias para el usuario." />
          </Form.Group>
          <Form.Group controlId="formNameUs">
            <Form.Label>Nombre:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí el nombre real del usuario." />
          </Form.Group>
          <Form.Group controlId="formPassUs">
            <Form.Label>Contraseña:</Form.Label>
            <Form.Control type="password" placeholder="Coloca aquí la contraseña del usuario." />
          </Form.Group>
          <Form.Group controlId="formDepartamentUs">
            <Form.Label>Departamento:</Form.Label>
            <Form.Control as="select" defaultValue="Todos">
              <option value="Inteligencia">Inteligencia</option>
              <option value="Intendencia">Intendencia</option>
              <option value="Operaciones">Operaciones</option>
              <option value="Todos">Todos</option>              
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formGradeUs">
            <Form.Label>Grado:</Form.Label>
            <Form.Control as="select" defaultValue="Operario">
              <option value="Presidente">Presidente</option>
              <option value="Vicepresidente">Vicepresidente</option>
              <option value="Gerente">Gerente</option>
              <option value="Supervisor">Supervisor</option>
              <option value="Operario">Operario</option>             
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formProfilePic">
            <Form.Label>Foto de perfil:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí una foto del usuario." />
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createUs(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createusmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createusmodal;