import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  
  var old = props.datos.datos;

  function Editstmodal(){
    // Datos metidos en el formulario:
    var model = document.querySelector('#formModelVeh').value;
    var color = document.querySelector('#formColorVeh').value;
    var owner = document.querySelector('#formOwnerVeh').value;
    var key = document.querySelector('#formKeyVeh').value;
    var description = document.querySelector('#formDescVeh').value;
    var departament = document.querySelector('#formDepartVeh').value;
    var state = document.querySelector('#formStateVeh').value;
    var vehicle_pic = document.querySelector('#formPicVeh').value;

    // Para saber quien edita:
    var userName = sessionStorage.getItem('userName');
    var timeStamp = Date.now();
    var created_by = old.created_by ? old.created_by : 'valkiria';

    db.collection("vehicles").where("model", "==", old.model)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(function(doc) {        
        db.collection("vehicles").doc(doc.id).set({
          'model': model,
          'description': description,
          'owner': owner,
          'key': key,
          'color': color,
          'vehicle_pic': vehicle_pic,
          'departament': departament,
          'state': state,
          'created_by': created_by,
          'edited_by': userName,
          'last_updated': timeStamp
        })
        .then(function() {
          console.log("Document successfully written!");
          window.location.reload(false);
        })
        .catch(function(error) {
          console.error("Error writing document: ", error);
        });
      }) 
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Editar Propiedad
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
        <Form.Group controlId="formModelVeh">
            <Form.Label>Modelo del Vehículo:</Form.Label>
            <Form.Control type="text" required defaultValue={old.model} />
          </Form.Group>
          <Form.Group controlId="formColorVeh">
            <Form.Label>Color: </Form.Label>
            <Form.Control type="text" defaultValue={old.color}/>
          </Form.Group>  
          <Form.Group controlId="formDescVeh">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control  as="textarea" rows={3} required defaultValue={old.description} />
          </Form.Group>
          <Form.Group controlId="formOwnerVeh">
            <Form.Label>Propietario / Oficina:</Form.Label>
            <Form.Control type="text" defaultValue={old.owner} />
          </Form.Group>
          <Form.Group controlId="formKeyVeh">
            <Form.Label>Llave:</Form.Label>
            <Form.Control type="text" defaultValue={old.key}/>
          </Form.Group>
          <Form.Group controlId="formStateVeh">
            <Form.Label>Estacionamiento</Form.Label>
            <Form.Control type="text" defaultValue={old.state}/>
          </Form.Group>         
          <Form.Group controlId="formPicVeh">
            <Form.Label>Imagen vehículo:</Form.Label>
            <Form.Control type="text" defaultValue={old.vehicle_pic}/>
          </Form.Group>
          <Form.Group controlId="formDepartVeh">
            <Form.Label>Departamento:</Form.Label>
            <Form.Control as="select" defaultValue={old.departament}>
              <option value="Inteligencia">Inteligencia</option>
              <option value="Intendencia">Intendencia</option>
              <option value="X-Negocios">Negocios</option>
              <option value="Operaciones">Operaciones</option>
              <option value="Personal">Personal</option>              
            </Form.Control>
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => Editstmodal()} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editstmodal(datostabla) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-pencil-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
          <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        datos={datostabla}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editstmodal;