import React from 'react';
import { Button, Modal, Image } from 'react-bootstrap';

function MyVerticallyCenteredModal(props) {

  var data = props.datos.datos;

  var userName = sessionStorage.getItem('userName');
  var fucEdit = new Date(data.last_updated);
  var fecha_ultedit = fucEdit.getDate()+'-'+(fucEdit.getMonth()+1)+'-'+fucEdit.getFullYear()+'  '+fucEdit.getHours()+':'+(fucEdit.getMinutes());


  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {data.model} - {data.owner}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="text-center">
          {data.vehicle_pic ? <Image src={data.vehicle_pic} height="350px"></Image> : <strong>No hay foto.</strong>}
        </div>   
        <p>
          <b>Modelo:</b><br></br>
          <div className="ml-3">{data.model ? data.model : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Color:</b><br></br>
          <div className="ml-3">{data.color ? data.color : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Propietario / Oficina:</b>
          <div className="ml-3">{data.owner ? data.owner : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Llave:</b><br></br>
          <div className="ml-3">{data.key ? data.key : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Estacionamiento:</b>
          <div className="ml-3">{data.state ? data.state : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Descripción:</b>
          <div className="ml-3">{data.description ? data.description : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Departamento:</b>
          <div className="mx-5">
          {(data.departament === 'Inteligencia') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-binoculars-fill text-info" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                      <path d="M4.5 1A1.5 1.5 0 0 0 3 2.5V3h4v-.5A1.5 1.5 0 0 0 5.5 1h-1zM7 4v1h2V4h4v.882a.5.5 0 0 0 .276.447l.895.447A1.5 1.5 0 0 1 15 7.118V13H9v-1.5a.5.5 0 0 1 .146-.354l.854-.853V9.5a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5v.793l.854.853A.5.5 0 0 1 7 11.5V13H1V7.118a1.5 1.5 0 0 1 .83-1.342l.894-.447A.5.5 0 0 0 3 4.882V4h4zM1 14v.5A1.5 1.5 0 0 0 2.5 16h3A1.5 1.5 0 0 0 7 14.5V14H1zm8 0v.5a1.5 1.5 0 0 0 1.5 1.5h3a1.5 1.5 0 0 0 1.5-1.5V14H9zm4-11H9v-.5A1.5 1.5 0 0 1 10.5 1h1A1.5 1.5 0 0 1 13 2.5V3z"/>
                                                  </svg> : ''}
          {(data.departament === 'Intendencia') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-book-half text-success" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                      <path fillRule="evenodd" d="M8.5 2.687v9.746c.935-.53 2.12-.603 3.213-.493 1.18.12 2.37.461 3.287.811V2.828c-.885-.37-2.154-.769-3.388-.893-1.33-.134-2.458.063-3.112.752zM8 1.783C7.015.936 5.587.81 4.287.94c-1.514.153-3.042.672-3.994 1.105A.5.5 0 0 0 0 2.5v11a.5.5 0 0 0 .707.455c.882-.4 2.303-.881 3.68-1.02 1.409-.142 2.59.087 3.223.877a.5.5 0 0 0 .78 0c.633-.79 1.814-1.019 3.222-.877 1.378.139 2.8.62 3.681 1.02A.5.5 0 0 0 16 13.5v-11a.5.5 0 0 0-.293-.455c-.952-.433-2.48-.952-3.994-1.105C10.413.809 8.985.936 8 1.783z"/>
                                                  </svg> : ''}
          {(data.departament === 'Operaciones') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-shield-shaded text-danger" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                          <path fillRule="evenodd" d="M5.443 1.991a60.17 60.17 0 0 0-2.725.802.454.454 0 0 0-.315.366C1.87 7.056 3.1 9.9 4.567 11.773c.736.94 1.533 1.636 2.197 2.093.333.228.626.394.857.5.116.053.21.089.282.11A.73.73 0 0 0 8 14.5c.007-.001.038-.005.097-.023.072-.022.166-.058.282-.111.23-.106.525-.272.857-.5a10.197 10.197 0 0 0 2.197-2.093C12.9 9.9 14.13 7.056 13.597 3.159a.454.454 0 0 0-.315-.366c-.626-.2-1.682-.526-2.725-.802C9.491 1.71 8.51 1.5 8 1.5c-.51 0-1.49.21-2.557.491zm-.256-.966C6.23.749 7.337.5 8 .5c.662 0 1.77.249 2.813.525a61.09 61.09 0 0 1 2.772.815c.528.168.926.623 1.003 1.184.573 4.197-.756 7.307-2.367 9.365a11.191 11.191 0 0 1-2.418 2.3 6.942 6.942 0 0 1-1.007.586c-.27.124-.558.225-.796.225s-.526-.101-.796-.225a6.908 6.908 0 0 1-1.007-.586 11.192 11.192 0 0 1-2.417-2.3C2.167 10.331.839 7.221 1.412 3.024A1.454 1.454 0 0 1 2.415 1.84a61.11 61.11 0 0 1 2.772-.815z"/>
                                                          <path d="M8 2.25c.909 0 3.188.685 4.254 1.022a.94.94 0 0 1 .656.773c.814 6.424-4.13 9.452-4.91 9.452V2.25z"/>
                                                      </svg> : ''}
          {(data.departament === 'Personal') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" className="bi bi-door-open-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                      <path fillRule="evenodd" d="M1.5 15a.5.5 0 0 0 0 1h13a.5.5 0 0 0 0-1H13V2.5A1.5 1.5 0 0 0 11.5 1H11V.5a.5.5 0 0 0-.57-.495l-7 1A.5.5 0 0 0 3 1.5V15H1.5zM11 2v13h1V2.5a.5.5 0 0 0-.5-.5H11zm-2.5 8c-.276 0-.5-.448-.5-1s.224-1 .5-1 .5.448.5 1-.224 1-.5 1z"/>
                                                  </svg> : ''}
          {(data.departament === 'X-Negocios') ? <svg width="1.5em" height="1.5em" viewBox="0 0 16 16" class="bi bi-cash-stack text-warning" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                          <path d="M14 3H1a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1h-1z"/>
                                                          <path fillRule="evenodd" d="M15 5H1v8h14V5zM1 4a1 1 0 0 0-1 1v8a1 1 0 0 0 1 1h14a1 1 0 0 0 1-1V5a1 1 0 0 0-1-1H1z"/>
                                                          <path d="M13 5a2 2 0 0 0 2 2V5h-2zM3 5a2 2 0 0 1-2 2V5h2zm10 8a2 2 0 0 1 2-2v2h-2zM3 13a2 2 0 0 0-2-2v2h2zm7-4a2 2 0 1 1-4 0 2 2 0 0 1 4 0z"/>
                                                      </svg> : ''}
          </div>
        </p>
        { userName === 'valkiria' ? 
          <div>
            <p><b>Creado por:</b><div className="ml-3">{data.created_by ? data.created_by : ''}</div></p>
            <p><b>Editado por:</b><div className="ml-3">{data.edited_by ? data.edited_by : ''}</div></p>
            <p><b>Última actualización:</b><div className="ml-3">{fecha_ultedit}</div></p>
          </div>
          : ''
        }
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Infovhmodal(props) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-eye-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z"/>
          <path fillRule="evenodd" d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        datos={props}
      />
    </>
  );
}

export default Infovhmodal;