import React from 'react';
import { Button, Modal, Image } from 'react-bootstrap';

function MyVerticallyCenteredModal(props) {

  var data = props.datos.datos;

  var userName = sessionStorage.getItem('userName');
  var fucEdit = new Date(data.last_updated);
  var fecha_ultedit = fucEdit.getDate()+'-'+(fucEdit.getMonth()+1)+'-'+fucEdit.getFullYear()+'  '+fucEdit.getHours()+':'+(fucEdit.getMinutes());


  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {data.name}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
      <p>
          <b>Categoría:</b><br></br>
          <div className="ml-3">{data.category ? data.category : 'No hay información.'}</div>       
        </p>   
        <p>
          <b>Cantidad:</b><br></br>
          <div className="ml-3">{data.quantity ? data.quantity : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Precio Proveedor:</b>
          <div className="ml-3">{data.supplier_price ? data.supplier_price+' $' : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Precio Coste Unidad:</b>
          <div className="ml-3">{data.cost_price ? data.cost_price+' $' : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Precio Venta Unidad:</b>
          <div className="ml-3">{data.sale_price ? data.sale_price+' $' : 'No hay información.'}</div>  
        </p>
        { userName === 'valkiria' ? 
          <div>
            <p><b>Creado por:</b><div className="ml-3">{data.created_by ? data.created_by : ''}</div></p>
            <p><b>Editado por:</b><div className="ml-3">{data.edited_by ? data.edited_by : ''}</div></p>
            <p><b>Última actualización:</b><div className="ml-3">{fecha_ultedit}</div></p>
          </div>
          : ''
        }
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Infodrmodal(props) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-eye-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z"/>
          <path fillRule="evenodd" d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        datos={props}
      />
    </>
  );
}

export default Infodrmodal;